<template>
    <div class="login-container-layout">
        <div class="login-container">
            <p class="login-title">{{joinTypeText}}</p>
            <form novalidate @submit.prevent="validate">
                <ul class="input-ul">
                    <li v-if="role=='busi'">
                        <div class="label"></div>
                        <div class="auth-item">
                            <div class="input-text-box">
                                <v-text-field
                                        v-model="busiName"
                                        :error-messages="busiNameErrors"
                                        outlined
                                        @input="$v.busiName.$touch()"
                                        @blur="$v.busiName.$touch()"
                                        placeholder=" "/>
                            </div>
                        </div>
                    </li>
                    <li>
                        <div class="label"></div>
                        <div class="auth-item">
                            <div class="input-text-box">
                                <v-text-field
                                        v-model="uid"
                                        :error-messages="uidErrors"
                                        outlined
                                        @input="$v.uid.$touch()"
                                        @blur="$v.uid.$touch()"
                                        placeholder=" "/>
                                <div class="error-message" v-if="uidDouble">  </div>
                            </div>
                        </div>

                    </li>
                    <li>
                        <div class="label"></div>

                        <v-text-field type="password"
                                      outlined
                                      :error-messages="upassErrors"
                                      @input="$v.upass.$touch()"
                                      @blur="$v.upass.$touch()"
                                      v-model="upass"
                                      placeholder=" "/>

                    </li>
                    <li>
                        <div class="label"> </div>
                        <v-text-field type="password"
                                      outlined
                                      :error-messages="reUpassErrors"
                                      @input="$v.reUpass.$touch()"
                                      @blur="$v.reUpass.$touch()"
                                      v-model="reUpass"
                                      placeholder=" "/>

                    </li>
                    <li>
                        <div class="label"></div>
                        <div class="auth-item">
                            <div class="input-text-box">
                                <v-text-field
                                        v-model="email"
                                        :error-messages="emailErrors"
                                        outlined
                                        @input="$v.email.$touch()"
                                        @blur="$v.email.$touch()"
                                        placeholder=" "/>
                                <div class="error-message" v-if="emailDouble">  </div>
                            </div>
                        </div>
                    </li>
                    <li>
                        <div class="label"></div>
                        <v-text-field
                                :error-messages="nameErrors"
                                outlined
                                @input="$v.name.$touch()"
                                @blur="$v.name.$touch()"
                                placeholder=" "
                                v-model="name"/>
                    </li>
                    <li>
                        <div class="label">()</div>
                        <div class="input-text-box">
                            <v-text-field
                                    :error-messages="nickErrors"
                                    outlined
                                    @input="$v.nick.$touch()"
                                    @blur="$v.nick.$touch()"
                                    placeholder=" "
                                    v-model="nick"/>

                            <div class="error-message" v-if="nickDouble">  </div>
                        </div>
                    </li>
                    <li>
                        <div class="check-box">
                            <v-checkbox
                                    color="#000"
                                    label="   ,      ."
                                    value="yes"
                                    v-model="emailSend"/>

                        </div>
                    </li>

                </ul>
                <div class="button-row">
                    <v-btn type="submit" class="blackButton">
                        
                    </v-btn>
                </div>
            </form>
            <div class="sns-box" v-if="snsList.length>0">
                <ul class="sns-ul">
                    <li class="sns-li" v-for="sns in snsList" :key="sns.code" @click="snsLogin(sns.code)">
                        <img :src="sns.img"><span>{{sns.title}}</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
    import {validationMixin} from 'vuelidate'
    import {
        required,
        email,
        minLength,
        maxLength,
        between,
        alphaNum,
        numeric,
        alpha,
        or
    } from 'vuelidate/lib/validators'
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_MEMBER_CONFIG,
        API_URL_GET_MEMBER_CHECK_ID,
        API_URL_GET_MEMBER_CHECK_NICK,
        //API_URL_SEND_MEMBER_AUTH_EMAIL,
        //API_URL_SEND_MEMBER_AUTH_PCS,
        //API_URL_GET_MEMBER_AUTH_CONFIRM,
        API_URL_GET_MEMBER_CHECK_EMAIL,
        API_URL_MEMBER_JOIN,

    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import {mapGetters} from "vuex";


    export default {
        mixins: [validationMixin],
        validations: {
            uid: {
                required,
                minLength: minLength(4),
                maxLength: maxLength(15),
                customval: or(alpha, alphaNum),
            },
            email: {required, email},
            upass: {required, minLength: minLength(6), maxLength: maxLength(20)},
            reUpass: {required},
            name: {required},
            nick: {},
            busiName: {required}

        },
        name: 'join',
        computed: {
            ...mapGetters('config', ["getSns", "getSettingActive"]),
            busiNameErrors() {
                const errors = []
                if (!this.$v.busiName.$dirty) return errors
                !this.$v.busiName.required && errors.push(' ')
                return errors;
            },
            uidErrors() {
                const errors = []
                if (!this.$v.uid.$dirty) return errors
                if (!this.$v.uid.required) {
                    errors.push(' ')
                }
                if (!this.$v.uid.minLength) {
                    errors.push(' 4~15   + ')
                }
                if (!this.$v.uid.maxLength) {
                    errors.push(' 4~15   + ')
                }
                if (!this.$v.uid.customval) {
                    errors.push(' 4~15   + ')
                }
                if (!isNaN(this.uid)) {
                    this.uidFail = true;
                    errors.push(' 4~15   + ')
                }
                if (errors.length < 1) {

                    const response = apiService.asyncPost(API_URL_GET_MEMBER_CHECK_ID, {uid: this.uid});
                    response.then((res) => {
                        switch (res.data.status) {
                            case 'fail':
                            case 'message':
                                this.uidDouble = true;
                                break;
                            case 'success':
                                this.uidDouble = false;
                                this.uidFail = false;
                                break;
                        }
                    })

                }
                return errors;

            },
            emailErrors() {
                const errors = []
                if (!this.$v.email.$dirty) return errors
                if (!this.$v.email.required) {
                    errors.push(' ')
                }
                if (!this.$v.email.email) {
                    errors.push('   ')
                }
                if (this.$v.email.required && this.$v.email.email) {
                    const response = apiService.asyncPost(API_URL_GET_MEMBER_CHECK_EMAIL, {email: this.email});
                    response.then((res) => {
                        switch (res.data.status) {
                            case 'fail':
                            case 'message':
                                this.emailDouble = true;
                                break;
                            case 'success':
                                this.emailDouble = false;
                                break;
                        }
                    })

                }
                return errors;

            },
            nameErrors() {
                const errors = []
                if (!this.$v.name.$dirty) return errors
                !this.$v.name.required && errors.push(' ')
                return errors;
            },
            nickErrors() {
                const errors = []
                if (!this.$v.nick.$dirty) return errors
                this.nickError = false;
                this.nickDouble = false;
                if (this.nick) {
                    const reg = /^[-|-|a-z|A-Z|0-9|]{2,10}$/;
                    if (!reg.test(this.nick)) {
                        errors.push(' ,, 2~10  ')
                        this.nickError = true;
                    } else {
                        const response = apiService.asyncPost(API_URL_GET_MEMBER_CHECK_NICK, {nick: this.nick});
                        response.then((res) => {
                            switch (res.data.status) {
                                case 'fail':
                                case 'message':
                                    this.nickDouble = true;
                                    break;
                                case 'success':
                                    this.nickDouble = false;
                                    break;
                            }
                        })
                    }

                }
                return errors;
            },
            upassErrors() {
                const errors = []
                this.upassErrorFlag = false;
                if (!this.$v.upass.$dirty) return errors
                !this.$v.upass.required && errors.push(' ')

                if (!this.$v.upass.minLength || !this.$v.upass.maxLength) {
                    errors.push(' 6~20  ')
                }

                let reg = /\d{1}/;
                let ret = reg.test(this.upass)
                if (!ret) {
                    this.upassErrorFlag = true;
                    errors.push('    ')
                }


                reg = /[a-zA-Z]/;
                ret = reg.test(this.upass)
                if (!ret) {
                    this.upassErrorFlag = true;
                    errors.push('   ')
                }
                reg = /[-|-|-]/;
                ret = reg.test(this.upass)
                if (ret) {
                    this.upassErrorFlag = true;
                    errors.push('   ')
                }
                return errors;
            },
            reUpassErrors() {
                const errors = []
                this.upassErrorFlag = false;
                if (!this.$v.reUpass.$dirty) return errors
                !this.$v.reUpass.required && errors.push('  ')
                if (this.upass != this.reUpass) {
                    this.upassErrorFlag = true
                    errors.push('   ')
                }
                return errors;
            },
        },
        watch: {
            getSettingActive(val) {
                if (val) {
                    this.settingSns();
                }
            }
        },
        data() {
            return {
                serverFlag: false,
                busiName: '',
                uid: '',
                uidFail: false,
                upass: '',
                reUpass: '',
                name: '',
                email: '',
                nick: '',
                emailSend: '',
                show1: false,
                settingInfo: {},
                uidDouble: false,
                nickDouble: false,
                emailDouble: false,
                nickError: false,
                emailError: false,
                role: 'indi',
                snsList: [],
                snsCode: '',
                upassErrorFlag: false,
                joinTypeText: '',
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.$router.push('/');
                return;
            }
            let flag = false;
            if (!sessionStorage.getItem("joinRole")) {
                flag = true;
            } else {
                switch (sessionStorage.getItem("joinRole")) {
                    case 'indi':
                    case 'busi':
                        this.role = sessionStorage.getItem("joinRole");
                        break;
                    default:
                        flag = true;
                        break;
                }

            }
            if (flag) {
                let params = {
                    message: ' ',
                    doAction: () => {
                        this.$router.push('/');
                    }
                };
                this.$eventBus.$emit('modalOpen', params);
            }
            if (this.role == 'busi') this.joinTypeText = ''

            if (this.getSettingActive) {
                this.settingSns();
            }
        },
        methods: {
            settingSns() {
                if (this.role == 'busi') return;
                this.snsList = [];
                for (const key in this.getSns) {
                    let img = '';
                    let title = '';
                    switch (key) {
                        case 'gl':
                            img = require('@/assets/img/ico_sns1.svg')
                            title = ' '
                            break;
                        case 'fb':
                            img = require('@/assets/img/ico_sns2.svg')
                            title = ' '
                            break;
                        case 'ka':
                            img = require('@/assets/img/ico_sns4.svg')
                            title = ' '
                            break;
                        case 'nv':
                            img = require('@/assets/img/ico_sns3.svg')
                            title = ' '
                            break;


                    }
                    if (this.getSns[key].key) {
                        let item = {
                            img: img,
                            code: key,
                            title: title
                        }
                        this.snsList.push(item);
                    }
                }
            },
            snsLogin(snsCode) {
                this.snsCode = snsCode;
                let redirect_uri = '';
                let url;
                let size;

                switch (snsCode) {
                    case 'ka':  //  

                        redirect_uri = this.getSns[snsCode].redirectUrl;
                        url = 'https://kauth.kakao.com/oauth/authorize?response_type=code&client_id=' + this.getSns[snsCode].key + '&redirect_uri=' + redirect_uri;
                        size = 'width=450,height=600,left=100,top=100'

                        break;
                    case 'nv':
                        redirect_uri = this.getSns[snsCode].redirectUrl;
                        url = 'https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id=' + this.getSns[snsCode].key + '&redirect_uri=' + redirect_uri;
                        size = 'width=600,height=600,left=100,top=100'
                        break;
                    case 'fb':
                        redirect_uri = this.getSns[snsCode].redirectUrl;
                        url = 'https://facebook.com/v2.11/dialog/oauth?client_id=' + this.getSns[snsCode].key + '&redirect_uri=' + redirect_uri;
                        size = 'width=600,height=600,left=100,top=100'

                        break;

                }
                const popup = window.open(url, 'snsLoginWin', size);
                if (location.hostname == 'localhost') {
                    setTimeout(() => {
                        popup.postMessage("check", "http://shop.riverflow.co.kr");
                    }, 1000);
                }


            },
            validate() {
                if (this.serverFlag) return;
                this.$v.$touch();
                let flag = false;
                for (const field in this.$v.$params) {
                    if (this.role == 'indi') {
                        if (field != 'busiName') {
                            if (this.$v[field].$anyError) flag = true;
                        }
                    } else {
                        if (this.$v[field].$anyError) flag = true;
                    }

                }
                if (flag) return;
                if (this.upassErrorFlag) return;

                if (this.nick) {
                    if (this.nickDouble) {
                        return;
                    }
                    if (this.nickError) {
                        return;
                    }
                }
                if (this.uidDouble || this.uidFail) {
                    return;
                }
                if (this.emailDouble) {
                    return;
                }
                const frm = {
                    uid: this.uid,
                    upass: this.upass,
                    name: this.name,
                    email: this.email,
                    nick: this.nick,
                    emailSend: this.emailSend,
                    role: this.role,
                    busiName: this.busiName
                }
                this.$eventBus.$emit('overlay', 'open');
                this.serverFlag = true;
                apiService.post(API_URL_MEMBER_JOIN, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let result = res.status;
                    let params = {}
                    if (result == 'success') {
                        switch (res.code) {
                            case 'emailAuth':
                                params = {
                                    message: '    .    ',
                                    doAction: () => {
                                        this.$router.push('/');
                                    }
                                };
                                break;
                            default:
                                params = {
                                    message: '  ',
                                    doAction: () => {
                                        this.$router.push('/memberLogin');
                                    }
                                };
                                break;
                        }


                    } else {
                        let msg = '';
                        switch (res.code) {
                            case 'doubleUid':
                                msg = '  '
                                break;
                            case 'doubleEmail':
                                msg = '  '
                                break;
                            case 'doubleNick':
                                msg = '  '
                                break;
                            default:
                                msg = ' '
                                break;
                        }
                        params = {
                            message: msg,
                        };
                    }
                    this.$eventBus.$emit('modalOpen', params);

                })
            },
        },
        mounted() {
        }

    }
</script>


<style lang="scss" scoped>
    .login-container {
        width: 636px;
        margin: 0 auto;
        padding: 36px 24px 50px 24px;
        background: #fff;

        .login-title {
            color: #333;
            font-size: 24px;
            font-weight: bold;
            margin-bottom: 35px;
            padding-bottom: 8px;
            border-bottom: solid 1px #333;
        }

        .w-full {
            width: 100%;
            margin: auto;

            .input-box {
                .label {
                    font-size: 14px;
                    color: #333;
                    padding-bottom: 5px;
                }

                width: 100%;
                margin: auto;
            }
        }

        .button-row {
            width: 100%;
            padding: 20px 0 20px 0;
        }

        .divide-line {
            width: 100%;
            margin: 20px 0 40px 0;
            border: 1px solid #DCDCDC;
            height: 0;
        }

        .sns-box {
            .sns-ul {
                padding: 0;
                margin: 0;

                li {
                    cursor: pointer;
                    width: 100%;
                    height: 48px;
                    border: solid 1px #dcdcdc;
                    border-radius: 5px;
                    margin: 10px 0;
                    text-align: center;
                    line-height: 48px;

                    img {
                        margin: 8px 10px 0 0;
                    }

                    span {
                        position: relative;
                        top: -8px;
                        font-size: 18px;
                        color: #333;
                        font-weight: bold;

                    }
                }
            }
        }

        .input-ul {
            padding: 0;
            margin: 0;

            li {
                .label {
                    padding: 0 0 5px 0;
                    font-weight: 600;
                    color: #000;
                    font-size: 14px;
                }

                .input-text-box {
                    width: 100%;
                    position: relative;

                    .error-message {
                        position: absolute;
                        bottom: 8px;
                        left: 10px;
                        color: #880E4F;
                        font-size: 12px;
                    }
                }

                .check-box {
                    margin-top: -10px;

                    ::v-deep .v-label {
                        font-size: 14px;
                        color: #333;
                        margin-left: -5px;
                    }
                }

                .auth-item {
                    display: flex;

                    .input-text-box {
                        width: 100%;
                        position: relative;

                        .error-message {
                            position: absolute;
                            bottom: 8px;
                            left: 10px;
                            color: #880E4F;
                            font-size: 12px;
                        }
                    }

                    .button-box {
                        margin-left: 8px;
                        min-width: 130px;

                        .send-button {
                            width: 100%;
                            color: #fff !important;
                            height: 54px !important;
                            font-size: 11px !important;
                        }
                    }

                }
            }
        }

    }

    @media screen and (min-width: 450px) and (max-width: 1259px) {
        .login-container-layout {
            padding-bottom:50px;
            .login-container {
                width: 90%;
                margin: 40px auto 0 auto;

            }
        }

    }

    @media screen and (max-width: 450px) {
        .login-container-layout {
            .login-container {
                width: 100%;
                margin: 0;
                padding: 36px 20px 50px 20px;

            }
        }
    }
</style>
